body {
  display: grid;
  grid-template: "hero header" auto "hero main" auto "hero footer" auto/ 50% 50%;
  height: 100vh; }

h1,
h2,
h3,
h4 {
  font-family: "Raleway", sans-serif; }

h2 {
  font-size: 2rem;
  font-weight: bold; }

h3 {
  font-weight: bold;
  margin-bottom: 0;
  padding-bottom: 0; }

p,
a,
input {
  font-family: "Open Sans", sans-serif;
  color: gray;
  line-height: 1.3rem; }

a,
a:visited,
a:focus {
  color: #070439; }

a:hover {
  color: #bbb8fa; }

body > div {
  flex: 1; }

.hero {
  grid-area: hero;
  display: flex;
  align-items: center;
  justify-content: center;
  height: 100%;
  background-image: linear-gradient(to bottom left, #000066, #666699); }
  .hero img {
    width: 75%; }

.page-header {
  grid-area: header;
  display: flex;
  align-items: flex-start;
  justify-content: space-between;
  padding: 1rem 3rem;
  /* White logo for mobile version only */ }
  .page-header .logo {
    display: block; }
  .page-header .logo-white {
    display: none; }
  .page-header .social a i::before {
    font-size: 1.3rem;
    padding-left: 1rem; }
  .page-header .social a,
  .page-header .social a:visited,
  .page-header .social a:focus {
    color: #000;
    text-decoration: none; }
  .page-header .social a:hover {
    color: #070439; }

main {
  grid-area: main;
  margin: 0rem 3rem;
  display: flex;
  flex-direction: column;
  justify-content: center; }
  main p {
    width: 80%; }
  main h3 {
    margin-bottom: 1rem; }
  main form {
    display: flex;
    flex-wrap: wrap;
    gap: 1rem; }
    main form input {
      flex: 1;
      border: none;
      border-radius: 6px;
      padding: 1rem; }
      main form input[type="email"] {
        flex-basis: 50%;
        box-shadow: 4px 6px 6px gray;
        min-width: 300px; }
      main form input[type="submit"] {
        color: white;
        background: #05d69b;
        cursor: pointer;
        flex: 0 140px; }
      main form input[type="submit"]:hover {
        background: #5fe3bd; }

footer {
  padding: 1rem 3rem;
  font-size: 0.8rem;
  text-align: right;
  margin-bottom: 1rem;
  align-self: end; }

/* Media queries */
@media only screen and (max-width: 850px) {
  body {
    background: url(../dist/img/bg-intro.svg) no-repeat center top;
    background-size: 100% 80%;
    grid-template: "header" auto "hero" auto "main" auto "footer" auto/ 100%;
    position: relative; }
  .page-header {
    position: relative; }
    .page-header .logo {
      display: none; }
    .page-header .logo-white {
      display: block; }
    .page-header .social i {
      color: white; }
  .hero {
    background: none; }
    .hero img {
      height: 320px;
      margin-top: 2rem;
      width: auto; }
  main {
    margin-top: 8rem; }
    main h3 {
      margin-bottom: 1.3rem; }
    main form {
      flex-wrap: wrap; }
    main input[type="email"] {
      box-shadow: 0px 0px 4px #ccc;
      flex-basis: 100%; }
    main input[type="submit"] {
      flex: 0 33%; }
  footer {
    margin-top: 8rem; } }

@media only screen and (max-width: 400px) {
  main p {
    width: 100%; }
  main form input[type="email"] {
    min-width: auto; } }
