* {
  box-sizing: border-box;
}

body {
  display: grid;
  grid-template:
    "hero header" auto
    "hero main" auto
    "hero footer" auto/
    50% 50%;
  height: 100vh;
}

h1,
h2,
h3,
h4 {
  font-family: "Raleway", sans-serif;
}

h2 {
  font-size: 2rem;
  font-weight: bold;
}

h3 {
  font-weight: bold;
  margin-bottom: 0;
  padding-bottom: 0;
}

p,
a,
input {
  font-family: "Open Sans", sans-serif;
  color: $gray;
  line-height: 1.3rem;
}

a,
a:visited,
a:focus {
  color: $darkBlue;
}

a:hover {
  color: $softBlue;
}

body > div {
  flex: 1;
}

.hero {
  grid-area: hero;
  display: flex;
  align-items: center;
  justify-content: center;

  height: 100%;
  background-image: linear-gradient(to bottom left, #000066, #666699);

  img {
    width: 75%;
  }
}

.page-header {
  grid-area: header;
  display: flex;
  align-items: flex-start;
  justify-content: space-between;
  padding: 1rem 3rem;

  .logo {
    display: block;
  }

  /* White logo for mobile version only */
  .logo-white {
    display: none;
  }

  .social a i::before {
    font-size: 1.3rem;
    padding-left: 1rem;
  }

  .social a,
  .social a:visited,
  .social a:focus {
    color: #000;
    text-decoration: none;
  }

  .social a:hover {
    color: $darkBlue;
  }
}

main {
  grid-area: main;
  margin: 0rem 3rem;
  display: flex;
  flex-direction: column;
  justify-content: center;

  h2 {
  }

  p {
    width: 80%;
  }

  h3 {
    margin-bottom: 1rem;
  }

  form {
    display: flex;
    flex-wrap: wrap;
    gap: 1rem;

    input {
      flex: 1;
      border: none;
      border-radius: 6px;
      padding: 1rem;

      &[type="email"] {
        flex-basis: 50%;
        box-shadow: 4px 6px 6px $gray;
        min-width: 300px;
      }

      &[type="submit"] {
        color: white;
        background: $green;
        cursor: pointer;
        flex: 0 140px;
      }
      &[type="submit"]:hover {
        background: $lightGreen;
      }
    }
  }
}

footer {
  padding: 1rem 3rem;
  font-size: 0.8rem;
  text-align: right;
  margin-bottom: 1rem;
  align-self: end;
}
